.drop-zone {
  min-width: 150px;
  max-width: 15vw;
  min-height: 30px;
  text-align: center;
  cursor: pointer;
  color: #ccc;
  border: 4px dashed #009578;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 5px;
  font-family: Quicksand, sans-serif;
  font-size: 18px;
  font-weight: 500;
  display: flex;
}

.drop-zone--over {
  border-style: solid;
}

.drop-zone__input {
  display: none;
}

.drop-zone__thumb {
  width: 100%;
  height: 100%;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  background-color: #ccc;
  background-size: cover;
  border-radius: 5px;
  overflow: hidden;
}

.drop-zone__thumb:after {
  content: attr(data-label);
  width: 100%;
  height: 100%;
  color: #061e3d;
  text-align: center;
  padding: 1vw;
  font-size: 14px;
}

.svgzrn {
  width: 30%;
  fill: green;
  margin: 1vw 35vw;
  padding: 0;
}

